import React from "react";

import { useContextValue } from "../SocketContext";

const Notifications = () => {
  const {
    call,
    callAccepted,
    answerCall,
  } = useContextValue();

  return (
    <div className="mb-5">
      {call.isReceivedCall && !callAccepted && (
        <div className="d-flex justify-content-center">
          <h3>{call.name} is calling: </h3>
          <button className="mx-3 btn btn-secondary" onClick={answerCall}>
            Answer
          </button>
        </div>
      )}
    </div>
  );
};

export default Notifications;
