import React, { useEffect, useState } from "react";
import MicIcon from "@material-ui/icons/Mic";
import MicOffIcon from "@material-ui/icons/MicOff";

import { useContextValue } from "../SocketContext";

const VideoPlayer = () => {
  const {
    mic,
    setMic,
    call,
    callAccepted,
    myVideo,
    userVideo,
    stream,
    name,
    callEnded,
  } = useContextValue();

  const [micUser, setMicUser] = useState(true);

  const [changeCol, setChangeCol] = useState("mx-auto col-md-8 col-sm-12 p-3");

  useEffect(() => {
    if (callAccepted) {
      setChangeCol("mx-auto col-md-6 col-sm-12 p-3");
    }
  }, [callAccepted]);

  const muteMicSelf = () => {
    setMic(!mic);
  };


  return (
    <div className="col-md-12">
      <div className="row">
        {/* our own video */}
        {stream && (
          <div className={changeCol}>
            <div>
              <h4>Your Name: {name || "name"}</h4>
            </div>
            <video
              className="w-100 border border-success"
              ref={myVideo}
              playsInline
              autoPlay
            />

            <div className="mt-2 text-center">
              <button
                className="btn btn-outline-secondary"
                onClick={muteMicSelf}
              >
                {mic ? (
                  <>
                    mute <MicOffIcon />
                  </>
                ) : (
                  <>
                    unmute <MicIcon />
                  </>
                )}
              </button>
            </div>
          </div>
        )}

        {/* our own video */}
        {callAccepted && !callEnded && (
          <div className={changeCol}>
            <div>
              <h4>{call.name || "boss"}</h4>
            </div>
            <video
              className="w-100 border border-success"
              ref={userVideo}
              playsInline
              autoPlay
              muted={micUser}
            />
            <div className="mt-2 text-center">
              <button
                className="btn btn-outline-secondary"
                onClick={() => setMicUser(!micUser)}
              >
                {micUser ? (
                  <>
                    unmute <MicIcon />
                  </>
                ) : (
                  <>
                    mute <MicOffIcon />
                  </>
                )}
              </button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default VideoPlayer;
