import React, { useState } from "react";
import { CopyToClipboard } from "react-copy-to-clipboard";
import { Phone, PhoneDisabled, Assignment } from "@material-ui/icons";
import { TextField } from "@material-ui/core";

import { useContextValue } from "../SocketContext";

const Optional = ({ children }) => {
  const {
    callAccepted,
    idConnect,
    name,
    callEnded,
    setName,
    callUser,
    leaveCall,
  } = useContextValue();

  const [idToCall, setIdToCall] = useState("");

  return (
    <div className="mt-3 col-md-12">
      <div className="mb-2">{children}</div>
      <div className="row">
        <div className="col-md-6 col-sm-12 p-3">
          {/* set name, get id */}
          <form className="bg-light p-3 rounded">
            <h4 className=" mb-3">Account Info</h4>
            <TextField
              label="Name"
              value={name}
              onChange={(e) => setName(e.target.value)}
              fullWidth
            ></TextField>

            <CopyToClipboard text={idConnect}>
              <button type="button" className="w-100 btn btn-primary mt-3">
                Copy your ID <Assignment fontSize="large" />
              </button>
            </CopyToClipboard>
          </form>
        </div>

        {/* another notification */}

        <div className="col-md-6 col-sm-12 p-3">
          {/* set name, get id */}
          <form className="bg-light p-3 rounded">
            <h4 className="mb-3">Make a call</h4>
            <TextField
              label="ID to call"
              value={idToCall}
              onChange={(e) => setIdToCall(e.target.value)}
              fullWidth
            ></TextField>
            {callAccepted && !callEnded ? (
              <button
                type="button"
                className="w-100 btn btn-danger mt-3"
                onClick={leaveCall}
              >
                Hang up <PhoneDisabled fontSize="large" />
              </button>
            ) : (
              <button
                type="button"
                className="w-100 btn btn-success mt-3"
                onClick={() => callUser(idToCall)}
              >
                Call <Phone fontSize="large" />
              </button>
            )}
          </form>
        </div>
        {/*  */}
      </div>
    </div>
  );
};

export default Optional;
